<template>
	<form class="cn"  @submit.prevent="onSubmit">
		<v-card>
			<v-layout row>
				<v-flex xs10>
					<v-card-title primary-title>
						<div class="headline">{{ title }}</div>
					</v-card-title>
				</v-flex>
				<v-flex xs2>
					<v-card-title primary-title>
						<div class="headline">
							<a href="#" @click.prevent="cancel">
								<v-icon>mdi-window-close</v-icon>
							</a>
						</div>
					</v-card-title>
					
				</v-flex>
			</v-layout>
			
			<v-card-text>
				<v-container grid-list-md>
					<v-layout wrap>
                  <v-flex xs12 sm4>
							<v-text-field v-model="model.client.accountId" label="AccountId"
								v-validate="'required|numeric'" 
								:error-messages="errors.collect('accountId')"
								name="accountId"
								data-vv-as="accountId"
								required
							/>
						</v-flex>
						<v-flex xs12 sm8>
							<v-text-field v-model="model.client.name" label="Name"
								v-validate="'required'" 
								:error-messages="errors.collect('name')"
								name="name"
								required
							/>
						</v-flex>
                  <v-flex xs12 sm4>
							<v-select :items="model.regions"  label="Region"
							v-model="model.client.region"
							/>
						</v-flex>
                  <v-flex xs12 sm8>
							<v-text-field v-model="model.client.address" label="Address"
								name="address"
							/>
						</v-flex>
					</v-layout>
					<ErrorList  />
				</v-container>
			</v-card-text>

			<v-card-actions>
				<v-spacer></v-spacer>
				<v-btn @click.prevent="cancel" color="blue darken-1" flat >Cancel</v-btn>
				<v-btn type="submit" @click.prevent="onSubmit" color="primary" flat>Save</v-btn>
			</v-card-actions>
		</v-card>
	</form>	
</template>


<script>

import { mapState } from 'vuex';
import ErrorList from '@/components/ErrorList';

export default {
	name: 'ClientEdit',
	components: {
      ErrorList
   },
	props: {
		model: {
         type: Object,
         default: null
      },
	},
	data () {
		return {
			
		}
	},
	computed: {
		title(){
			if(this.model.client && this.model.client.id) return 'Edit Client';
			return 'Create Client';			
		}
   },
	beforeMount(){
		
	},
	methods: {
		cancel(){
			this.$emit('cancel');
		},
		onSubmit() {
         this.$validator.validate().then(valid => {
            if(valid) this.$emit('submit');
         });         
      }
	}
}
</script>
