<template>
	<v-container v-if="pageList" fluid grid-list-xl fill-height>
     <v-layout justify-center  align-center>
			<v-flex xs12>
				<material-card>
					<v-card-text>
						<v-layout row wrap>
							<v-flex xs6 sm6 md4>
								<v-select v-model="params.region" :items="regionOptions"  label="Region" 
								/>
							</v-flex>
							<v-flex xs6 sm6 md4>
								<form @submit.prevent="search">
									<v-text-field
										v-model="params.keyword"
										append-icon="mdi-magnify"
										label="Search"
										single-line
										hide-details
									/>
								</form>
							</v-flex>
							<v-flex xs12 sm6 md4 class="text-lg-right">
								<v-tooltip top content-class="top">
									<v-btn :disabled="!canCreate" @click.prevent="create" class="mr-2" slot="activator"  color="info" icon>
										<v-icon>mdi-plus-circle</v-icon>
									</v-btn>
									<span class="cn">Add</span>
								</v-tooltip>
								<v-dialog v-model="deleting" width="480px">
									<Confirm :title="deleteConfirm.title"
									@ok="submitDelete" @cancel="cancelDelete" 
									/>
								</v-dialog>
				 				<v-dialog v-model="editting" persistent max-width="500px">
									<client-edit v-if="editting" :model="editModel" 
									@submit="submit" @cancel="cancelEdit"
									/>
      						</v-dialog>
							</v-flex>
						</v-layout>
						
						<v-layout row wrap>
							<v-flex sm12>
								<v-data-table :headers="headers" 
								:items="clients" hide-actions item-key="id"
								>
									<template slot="headerCell" slot-scope="{ header }">
										<span class="subheading font-weight-light text-success text--darken-3 cn">
											{{ header.text }}
										</span>
									</template>
									<template slot="items" slot-scope="props">
										<td>{{ props.item.accountId }}</td>
										<td class="cn">{{ props.item.name }} </td>
										<td>{{ props.item.region }}</td>
										<td>{{ props.item.address }}</td>
										<td>
											<v-tooltip top  content-class="top">
												<a href="#" @click.prevent="edit(props.item.id)"  slot="activator" icon>
													<v-icon small color="success">mdi-pencil</v-icon>
												</a>
												<span class="cn">Edit</span>
											</v-tooltip>
											<v-tooltip top  content-class="top">
												<a class="ml-3" href="#" @click.prevent="remove(props.item)"  slot="activator" icon>
													<v-icon small color="error">mdi-delete-circle</v-icon>
												</a>
												<span class="cn">Remove</span>
											</v-tooltip>
										</td>
									</template>
								</v-data-table>
								
							</v-flex>
						</v-layout>
						<table-pager :model="pageList" :canPage="false"/>
						
					</v-card-text>
				</material-card>
			</v-flex>
     </v-layout>
	</v-container>
    
	
</template>

<script>
import { mapState } from 'vuex';
import { CLEAR_ERROR, SET_ERROR } from '../store/mutations.type';

import { FETCH_CLIENTS, CREATE_CLIENT, STORE_CLIENT,
EDIT_CLIENT, UPDATE_CLIENT, DELETE_CLIENT } from '../store/actions.type';

import MaterialCard from '../components/material/Card';
import TablePager from '../components/TablePager';
import ClientEdit from '../components/client/Edit';
import Confirm from '@/components/Confirm';

export default {
	name: 'ClientsIndexView',
	components: {
		'material-card' : MaterialCard,
		'client-edit' : ClientEdit,
		'table-pager' : TablePager,
		Confirm
	},
	data () {
		return {
			params: {
				region: '',
				keyword: ''
			},
			headers: [
				{
					sortable: false,
					text: 'AccountId',
					value: 'accountId'
				},
				{
					sortable: false,
					text: 'Name',
					value: 'name'
				},
				{
					sortable: false,
					text: 'Region',
					value: 'region'
				},
				{
					sortable: false,
					text: 'Address',
					value: 'address'
				},
				{
					sortable: false,
					text: '',
					value: '',
					width: '100px'
				}
			],

			
			editting: false,
			deleting: false,
			selected: 0,

			editModel: null,

			deleteConfirm: {
				title: ''
			}
			
		}
	},
	computed: {
      ...mapState({
			pageList: state => state.client.pageList,
			regions: state => state.client.regions,
		}),
		regionOptions(){
			return [ {value: '', text: 'All Regions'}, ...this.regions];
		},
		clients(){
			return this.pageList ? this.pageList.viewList : [];
		},
		canRemove(){
			if(this.editting) return false; 
			return true;
		},
		canCreate(){
			return !this.editting && !this.deleting;
		}
	},
	beforeMount(){
		this.fetchData();
	},
	methods: {
		init(){
			this.fetchData();
			this.editModel = null;  
			this.editting = false;			
		},
		fetchData(){
			this.$store.commit(CLEAR_ERROR);
			this.$store.dispatch(FETCH_CLIENTS, this.params)
				.catch(error => {
					Bus.$emit('errors');
				})
		},
		search(){
			this.fetchData();
		},
		create(){
			this.$store.commit(CLEAR_ERROR);
			this.$store.dispatch(CREATE_CLIENT)
				.then(model => {
					this.editModel = model;  
					this.editting = true;
				})
				.catch(error => {
					Bus.$emit('errors');
				})
			
		},
		edit(id){
			this.$store.commit(CLEAR_ERROR);
			this.$store.dispatch(EDIT_CLIENT, id)
				.then(model => {
					this.editModel = model;  
					this.editting = true;
				})
				.catch(error => {
					Bus.$emit('errors');
				})
		},
      cancelEdit(){
			this.model = null;  
         this.editting = false;
		},
		remove(item){
			this.selected = item.id;
			this.deleteConfirm.title = `Are You Sure To Remove Client: ${item.name} ?`;
			this.deleting = true;
		},
		submitDelete(){
			this.$store.commit(CLEAR_ERROR);
			this.$store.dispatch(DELETE_CLIENT, this.selected)
				.then(() => {
					this.fetchData();
					this.model = null;  
					this.deleting = false;
				})
				.catch(error => {
					Bus.$emit('errors');
				})
		},
		cancelDelete(){
			this.deleting = false;
		},
      submit(){
			this.$store.commit(CLEAR_ERROR);
			let client = this.editModel.client;
			let action = client.id ? UPDATE_CLIENT : STORE_CLIENT;
         this.$store.dispatch(action, client)
				.then(() => {
					Bus.$emit('success');
					this.init();
				})
				.catch(error => {
					if(!error)  Bus.$emit('errors');
					else this.$store.commit(SET_ERROR, error);
				})
		}
		
	}
}
</script>



